.trusting {
  color: var(--colorWhite);

  .container {
    padding-right: 0;
    padding-left: 0;
    background-blend-mode: multiply;
    background: linear-gradient(to right top, #fff 70%, rgb(#14efb5, 0.2) 100%);
  }
  &__wrap {
    display: flex;

    @media (max-width: 768px) {
      flex-direction: column;
    }
  }

  &__left {
    height: 970px;
    flex: 0 1 50%;
    background-image: url("../img/union-left.png");
    background-position: 0 0;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &__right {
    height: 970px;
    flex: 0 1 50%;
    flex: 0 1 50%;
    background-image: url("../img/Union.png");
    background-position: 0 0;
    background-size: cover;
    background-repeat: no-repeat;
    @media (max-width: 768px) {
      background-position: 0 -55px;
      padding-bottom: 0;
      background-color: var(--colorMain);
    }
  }
}

.left-trusting {
  padding: 60px 0 0 53px;

  &__title {
    font-size: 48px;
    line-height: 53px;
    letter-spacing: 0.5px;
    max-width: 500px;
    @media (max-width: 1200px) {
      font-size: 35px;
      line-height: 42px;
    }
    @media (max-width: 992px) {
      font-size: 32px;
      line-height: 37px;
    }
    @media (max-width: 576px) {
      font-size: 32px;
      line-height: 37px;
      letter-spacing: 0.5px;
    }
  }

  &__img {
    position: relative;
    img {
      position: absolute;
      top: 113px;
      right: -38px;
      @media (max-width: 1421px) {
        top: 134px;
      }
      @media (max-width: 1206px) {
        top: 156px;
        right: -34px;
      }
      @media (max-width: 1200px) {
        top: 213px;
        right: -71px;
      }
      @media (max-width: 1121px) {
        top: 170px;
      }
      @media (max-width: 992px) {
        right: -47px;
      }
      @media (max-width: 978px) {
        top: 198px;
      }
      @media (max-width: 961px) {
        top: 164px;
      }
      @media (max-width: 837px) {
        top: 187px;
        right: -45px;
      }
      @media (max-width: 768px) {
        position: static;
      }
      @media (max-width: 576px) {
        margin-top: 20px;
        margin-bottom: 20px;
      }
    }
  }
}

.right-trusting {
  padding: 71px 73px 40px 51px;
  &__content {
    line-height: 26px;
    span {
      font-weight: 700;
    }
  }

  &__btn {
    display: inline-block;
    margin-top: 32px;
    &:hover {
      color: var(--colorWhite);
    }
  }

  &__img {
    position: relative;
    img {
      position: absolute;
      top: 50px;
      left: -83px;
      @media (max-width: 1300px) {
        left: -75px;
      }
      @media (max-width: 1200px) {
        left: -70px;
      }
      @media (max-width: 1058px) {
        top: 27px;
        left: -62px;
      }
      @media (max-width: 992px) {
        top: 17px;
        left: -47px;
      }
      @media (max-width: 837px) {
        left: -42px;
      }
      @media (max-width: 768px) {
        position: static;
      }
      @media (max-width: 576px) {
        margin-top: 20px;
        margin-bottom: 20px;
      }
    }
  }

  &__text {
  }
}
