html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  font-weight: 400;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
  color: var(--colorMain);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.btn-outline {
  padding: 8px 25px;
  border: 1px solid #14efb5;
  border-radius: 6px;
  font-weight: 500;
  font-size: 15px;
  line-height: 24px;
  color: var(--colorMain);
  transition: all 0.3s ease 0s;
  &:hover {
    background-color: var(--colorGreen);
  }
  @media (max-width: 768px) {
    padding: 6px 20px;
    font-size: 14px;
  }
}

.btn {
  padding: 8px 25px;
  border: 1px solid #14efb5;
  border-radius: 6px;
  font-weight: 500;
  font-size: 15px;
  line-height: 24px;
  color: var(--colorMain);
  transition: all 0.3s ease 0s;
  background-color: var(--colorGreen);
  &:hover {
    background-color: transparent;
  }
  @media (max-width: 768px) {
    padding: 6px 20px;
    font-size: 14px;
  }
}

.title {
  font-family: "Gotham";
  font-weight: 700;
  font-size: 44px;
  line-height: 53px;
  letter-spacing: 0.5px;
  @media (max-width: 1200px) {
    font-size: 35px;
    line-height: 42px;
  }
  @media (max-width: 992px) {
    font-size: 32px;
    line-height: 37px;
  }
  @media (max-width: 576px) {
    font-size: 32px;
    line-height: 37px;
    letter-spacing: 0.5px;
  }
}

.scroll-down {
  .container {
    position: relative;
  }

  img {
    position: absolute;
    //top: -110px;
    top: 680px;
    z-index: 100;
    right: 20px;
    @media (max-width: 1200px) {
      top: 580px;
    }
    @media (max-width: 768px) {
      right: 7px;
    }
  }
}
