.calculator {
  padding-top: 100px;
  padding-bottom: 100px;
  &__suptitle {
    text-align: center;
  }

  &__title {
    text-align: center;
  }

  &__wrap {
    margin-top: 50px;
    display: flex;
    justify-content: space-between;
  }

  &__left {
    flex: 0 1 50%;
  }

  &__right {
    flex: 0 1 50%;
  }
}

.left-calculator {
  padding-right: 113px;

  label {
    font-family: "Gotham";
    font-weight: 700;
    font-size: 26px;
    line-height: 31px;
    //display: block;
  }
  input {
    display: inline-block;
  }
  .slider {
    position: relative;
    appearance: none;
    width: 100%;
    //max-width: 557px;

    height: 20px;
    background: linear-gradient(90deg, var(--colorWhite) 68%, var(--colorWhite) 68%);
    border-radius: 10px;
    position: relative;
    box-shadow: 4px 4px 8px 2px rgba(78, 77, 77, 0.25);
  }
  .slider::-webkit-slider-thumb {
    appearance: none;
    width: 32px;
    height: 32px;
    cursor: pointer;
    background-image: url("../img/circle-slider.png");
    position: relative;
  }
  .slider::-moz-range-thumb {
    appearance: none;
    -moz-appearance: none;
    border-radius: 50%;
    border: none;
    outline: none;
    width: 32px;
    height: 32px;
    cursor: pointer;
    background-image: url("../img/circle-slider.png");
    position: relative;
  }
  #value {
    background: #ffffff;
    box-shadow: 4px 4px 8px 2px rgba(189, 189, 189, 0.25);
    border-radius: 5px;
    font-size: 16px;
    line-height: 26px;
    //margin-top: 30px;
    margin: 30px auto 0 auto;
    display: flex;
    justify-content: center;
    width: 40px;
    padding: 3px 5px;
  }
}
.right-calculator {
  display: flex;
  p {
    margin: 3px 0;
    font-size: 15px;
    line-height: 24px;
  }
  span {
    font-family: "Gotham";
    font-size: 48px;
    line-height: 53px;
    letter-spacing: 0.5px;
    font-weight: 700;
    display: block;
  }
  &__info {
  }

  &__btn {
    &:nth-of-type(1) {
      margin-top: 40px;
      margin-bottom: 24px;
    }
  }

  &__chart {
  }

  &__img {
  }
}
